import React, {useContext} from 'react'
import {BaskerImg, Checked} from "../img/_svg.jsx"
import {CostomContext} from "../Context.jsx"
import ReitingItem from "./_ratingItem.jsx"

export default function GoodsItem( props ) {
    const {BasketItems, shopGoods, LiketItems} = useContext(CostomContext);
    const {id, title, price, img, volume, item, hit, lean} = props;
    // const [AddOrder, setAddOrder] = useState(false);
    let newRating = [];
    for( let i = 0;i <= props.rating - 1;i++ ) {
        newRating[ i ] = {id: "img/star.svg"}
    }
    if( props.rating % 1 > 0 ) {
        let j = +props.rating + 1
        newRating[ j.toFixed( 0 ) ] = {id: "img/star_half.svg"}
    }
    const ItemIndex = shopGoods.findIndex( ( orderItems ) => 
            orderItems.id === id
         )

    return (
        <div className="goods__item__cart">
            <div className="cart__header">
                {hit ? <div className="hit">хит</div> : lean ? <div className="lean">постное</div> : <div></div>}
                <div className="cart__header-status">
                    <img className="cart__header-img" src={img} alt="" />
                </div>
            </div>
            <div className="cart__rating">
                {newRating.map( ( item, index ) =>
                    <ReitingItem 
                    key={"rating-" + index}
                    item={item} /> 
                )}
            </div>
            <div className="cart__title">
                {title}
            </div>
            <div className="cart__info">
                <span className="cart__info-price">
                    {price}
                </span>
                <span className="cart__info-volume">
                    за {volume}.
                </span>
            </div>
            <div style={{height: "7rem"}}>
                <div onClick={() => BasketItems( item )}
                    className="cart__add-order"
                >
                    <span className="cart__add-color">
                    </span>
                    <div style={{zIndex: "10"}}>
                      {ItemIndex < 0 ?  <BaskerImg /> : <Checked width={"2.2rem"} height={"1.2rem"}/>}
                    </div>
                </div>
                <div onClick={() => LiketItems( item )} className="cart__like-order">
                    <img style={{width: "1.6rem", height: "1.4rem"}} src="img/heartblack.png" alt="сердце"/>
                </div>
            </div>
        </div>
    )
}

