import React, {createContext, useState, useEffect} from 'react'


export const CostomContext = createContext()

export const Context = ( props ) => {
    const [ goods, setGoods ] = useState( [] ),
        [ recipe, setRecipe ] = useState( [] ),
        [ elements, setElements ] = useState( [] ),
        [ category, setCategory ] = useState( [] ),
        [ isloading, setIsloading ] = useState( true ),
        [ error, setError ] = useState( false ),
        [ shopGoods, setShopGoods ] = useState( [] ),
        [ likeGoods, setLikeGoods ] = useState( [] ),
        [ currentPage, setcurrentPage ] = useState( 1 ),
        [Win_width, setWin_width] = useState(document.documentElement.clientWidth),
        postPerPage = 6,
        [ newgoods, setnewgoods ] = useState( [] );
        


    useEffect( () => {
        if( isloading ) {
            fetch( "data/goods.json" )
                .then( ( response ) => response.json() )
                .then( ( response ) => {
                    setCategory( response.category );
                    setElements( response.goods );
                    setRecipe( response.recipe );
                    if( error ) setError( !error )
                } )
                .catch( ( err ) => {console.log( "Ошибка:" + {err} ); setError( !error )} )
            if( goods.length > 0 ) {
                setIsloading( !isloading )
            } else {
                setTimeout( () => {
                    console.log( "Ошибка:" ); setError( !error )
                }, 2000 )
            }
        }
    }, [ error, isloading, goods ] )

    // Получаем текущий размер страницы
    window.addEventListener("resize", () => setWin_width(document.documentElement.clientWidth))


    //Запись данных в массив товаров для вывода
    useEffect( () => {
        category.map( ( item ) =>
            item.state === true &&
            setGoods( elements.filter( el => el.category === item.id ) )

        )
    }, [ category, elements ] )

    const IndexofLastPost = currentPage * postPerPage // последний пост
    const IndexofFirstPost = IndexofLastPost - postPerPage // Первый пост


    useEffect( () => {
        setnewgoods( goods.slice( IndexofFirstPost, IndexofLastPost ) ) //Посты к 
    }, [ goods, currentPage, IndexofFirstPost, IndexofLastPost ] )

    const paginete = ( pageNumber ) => {
        setcurrentPage( pageNumber )
    }


    const BasketItems = ( goods ) => { //Элементы в корзине
        const ItemIndex = shopGoods.findIndex( ( orderItems ) =>
            orderItems.id === goods.id
        )
        if( ItemIndex < 0 ) {
            const newItem = {
                ...goods,
                quantity: 1
            }
            setShopGoods( [ newItem, ...shopGoods ] )
        } else {
            setShopGoods( shopGoods )
        }

    }
    const LiketItems = ( goods ) => {
        const ItemIndex = likeGoods.findIndex( ( orderItems ) =>
            orderItems.id === goods.id
        )
        if( ItemIndex < 0 ) {
            const newItem = {
                ...goods,
                quantity: 1
            }
            setLikeGoods( [ newItem, ...likeGoods ] )
        } else {
            setLikeGoods( shopGoods )
        }
    }

    const FilterCategory = ( id ) => {
        setGoods( elements.filter( item => item.category === id ) )
        setCategory( category.map( item => {
            if( item.id === id ) {
                return {...item, state: true}
            }
            else {
                return {...item, state: false}
            }
        }
        ) )
        setcurrentPage( 1 )
    }
    // что мы хотим передать с провайдером
    const value = {
        goods,
        isloading,
        setIsloading,
        shopGoods,
        category,
        BasketItems,
        FilterCategory,
        paginete,
        currentPage,
        postPerPage,
        newgoods,
        LiketItems,
        elements,
        recipe,
        Win_width
    }

    return <CostomContext.Provider value={value}>
        {props.children}
    </CostomContext.Provider>

}