import {TimeImg} from "../img/_svg.jsx"


export default function SliderItem({x, item}) {


    return (
        <div 
        style={{transform: `translateX(-${x}%)`}} 
        className="slider__item"
        >
            <div>
                <img src={item.img} alt="" />
            </div>
            <div className="slider--info">
                <div className="slider--info-title">
                    {item.title}
                </div>
                <div className="slider--info-text">
                    <TimeImg /> {item.time}
                </div>
            </div>
        </div>
    )
}