import React, {useContext, useState} from 'react'
import {CostomContext} from "../Context.jsx"
import "./slider.scss"
import SliderNav from "./_sliderNav.jsx"
import SliderItem from "./_sliderItem.jsx"

export default function Slider() {
    const {recipe} = useContext( CostomContext )
    const [ x, setX ] = useState( 0 )

    const position = ( index ) => {
        setX( index * 100 )
    }
    
    return (
        <>
            <div
            className="slider--nav">
                {recipe.map( ( item, index ) => 
                    <SliderNav 
                    key={"nav-"+index}
                    index={index}
                    item={item}
                    recipe={recipe}
                    x={x}
                    position={position}
                    />
                )}
            </div>
            <div className="slider__inner" >
                <div className="slider" >
                    {
                        recipe.map( item  => 
                            <SliderItem 
                            key={item.id}
                            item={item}
                            x={x}
                            />
                         )
                    }
                </div>
            </div>
        </>
    )
}