import React, {useContext} from 'react'
import {CostomContext} from "../Context.jsx"

export default function CategoryItem(props){
    const {id, name, state} = props;
    const {FilterCategory} = useContext(CostomContext)


    return( 
        <li className={state ? "color_text_black" : "color_text_gray"} onClick={()=> FilterCategory(id)}>{name}</li>
    )
}