import React, {useEffect, useState, useContext} from 'react'
import {BaskerImg,Search} from "../img/_svg.jsx"
import {CostomContext} from "../Context.jsx"

export default function HeaderBot( props ) {
    const {shopGoods = [], Win_width} = useContext(CostomContext);
    const   [shutters, setShutters] = useState("");
    //Активация элемента Реф при достижении его скрола.
    useEffect(()=>{ 
        if(Win_width >= 1046){
            setShutters("")
        if( window.pageYOffset >= 78){
        setShutters("header__bot_shut");
        } else setShutters("")
        window.onscroll = () =>{ 
        if( window.pageYOffset >= 78){
        setShutters("header__bot_shut");
        } else setShutters("")
    }}
    },[Win_width])

    let shopGoodsPrice = 0;
    shopGoods.map(item => shopGoodsPrice += +item.price);

    return (
        <div onClick={()=> console.log(Win_width)} className={shutters ? (shutters + " header__bottom") : " header__bottom"}>
            <div className="header__bottom__inner">
                <a href="#!">
                    <div className="nav__wrapper">
                        <figure className="icon-burger">
                            <span className="line"></span>
                            <span className="line"></span>
                            <span className="line"></span>
                        </figure>
                        <div className="text"> Каталог продуктов </div>
                    </div>
                </a>
                <div className="header__form">
                    <form >
                        <input className="header__form__input" placeholder="Что бы вы хотели сегодня на ужин?"
                            type="text"
                            autoComplete="off"
                        />
                        <button type='submit' className="header__from__input--search btn-search">
                            <Search 
                            width={Win_width <= 768 ? "2rem" : "3vw"} 
                            height={Win_width <= 768 ? "1.4rem" : "2vw"} 
                            fill={Win_width <= 768 ? "#fff" : "#000" } 
                            />
                        </button>
                    </form>
                </div>
                {Win_width <= 768 && (
                <a href="/">
                    <img 
                        style={{
                            padding: "2rem 7vw",
                            width: "18vw", 
                            height: "auto"}} 
                        src="img/logowhite.png" 
                        alt="a"/>
                </a>)}
                <div className="header__heart">
                    <a href="#!">
                        <img src="img/heart.png" alt="понравилось" />
                    </a>
                </div>
                <div className="header__shop">
                    <div >
                        <BaskerImg 
                        width={Win_width <= 768 ? "2rem" : "3vw"} 
                        height={Win_width <= 768 ? "1.4rem" : "2vw"} 
                        />
                    </div>
                    <div className="header__shop-count">
                        <span className="bgc-black">
                            {shopGoods.length}
                        </span>
                    </div>
                    <span className="header_shop-price">
                        {shopGoodsPrice}
                    </span>
                </div>
            </div>

        </div>
    )
}