

export default function SliderNav({recipe = [], index, x, position = Function.prototype}) {


    return  index <= recipe.length - 4  && (
            <div
                key={"slide-nav" + index}
                onClick={() => position( index )}
                style={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center"
                }}>
                <span
                    key={index + "el--slider"}
                    className={( x / 100 !== index )
                        ? "slider--nav-items" : "slider--nav-items items-active"}>
                </span>
            </div>
             )

}