import React, {useContext} from 'react'
import GoodsItem from "./_ItemGoods.jsx"
import ContainerHeader from "./_header.jsx"
import "./content.scss"
import {CostomContext} from "../Context.jsx"
import Pagination from "../Pagination/_pagination.jsx"
import Preloader from "../Preloader/Preloader.jsx"

import Slider from "../Slider//Slider.jsx"


export default function ContainerGoods( props ) {
    const {FilterCategory, 
        newgoods, 
        category, 
        isloading} = useContext( CostomContext )
    return (
        <div className="container__inner">
            <ContainerHeader
                categoty={category}
                FilterCategory={FilterCategory} />
            {!isloading ? (<>
            <div className="container__goods">
                {newgoods.map( ( item, index ) =>
                    <GoodsItem
                        key={item.id + index}
                        item={item}
                        shopGoods={props.shopGoods}
                        {...item} />
                )}
            </div>
            <Pagination />
            </>) : (<Preloader />)}
            <div className="container__inner-info">
                <span>
                    Рецепты с данным продуктом
                </span>
                <span>
                    <Slider />
                </span>

            </div>
        </div>)
        
    
}